.products {
  overflow: hidden; }
  .products-header {
    height: 600px;
    margin-bottom: 70px; }
    @media (max-width: 575.9px) {
      .products-header {
        height: 250px; } }
    @media (max-width: 767.9px) {
      .products-header {
        height: 300px; } }
    @media (max-width: 991.9px) {
      .products-header {
        height: 350px; } }
    .products-header img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      -o-object-fit: cover;
         object-fit: cover; }
    .products-header .overlay {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #000;
      opacity: 0.6; }
    .products-header h1 {
      text-align: center;
      color: #ad633e;
      top: 50%;
      -webkit-transform: translateY(-50%);
          -ms-transform: translateY(-50%);
              transform: translateY(-50%); }
  .products-wrapper {
    width: 100%;
    display: grid; }
    @media (min-width: 576px) {
      .products-wrapper {
        grid-template-columns: repeat(2, 1fr); } }
    @media (min-width: 768px) {
      .products-wrapper {
        grid-template-columns: repeat(3, 1fr); } }
    @media (min-width: 992px) {
      .products-wrapper {
        grid-template-columns: repeat(4, 1fr); } }



