.product-cocktails {
  width: 100%;
  padding-top: 15px; }
  @media (min-width: 768px) {
    .product-cocktails {
      width: 100%; } }
  @media (min-width: 992px) {
    .product-cocktails {
      padding-left: 30px; } }
  .product-cocktails .title {
    margin-bottom: 15px; }
  .product-cocktails .cocktails {
    display: grid; }
    @media (min-width: 768px) {
      .product-cocktails .cocktails {
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 15px; } }
    @media (min-width: 1200px) {
      .product-cocktails .cocktails {
        grid-template-columns: repeat(3, 1fr); } }
  .product-cocktails .cocktail {
    display: block;
    width: 100%;
    height: calc(100vw - 30px);
    margin-bottom: 20px; }
    @media (min-width: 768px) {
      .product-cocktails .cocktail {
        height: calc(50vw - 20px); } }
    @media (min-width: 992px) {
      .product-cocktails .cocktail {
        height: calc(50vw - 130px); } }
    @media (min-width: 1200px) {
      .product-cocktails .cocktail {
        height: 320px; } }
    .product-cocktails .cocktail-img {
      width: 100%;
      height: calc(100% - 40px);
      margin-bottom: 5px; }
      .product-cocktails .cocktail-img img {
        -o-object-fit: cover;
           object-fit: cover;
        width: 100%;
        height: 100%; }
    .product-cocktails .cocktail-name {
      text-align: center; }



